body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*======================================
//--//-->   STYLES GENERALES
======================================*/
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #505050;
}




.bg-image {
  /*background-image: url("https://i.imgur.com/eQOTYLm.jpg");*/
  background-image: url('https://i.imgur.com/t2KwTJk.jpg'); 
}

.bg-image {
  min-height: 100vh;
  background-repeat: repeat;
  background-size: auto;
  /*background-attachment: fixed;*/
  
}

.bg-pb {
  padding-bottom: 100px;
}

.page-view {
 
  position: relative;
}

.p-r {
  position: relative;
}

.p-a {
  position: absolute;
}

/*--/ Sections /--*/

.sect-1 {
  padding: 1rem 0;
}

.sect-pt1 {
  padding-top: 1rem;
}

.sect-pb1 {
  padding-bottom: 1rem;
}

.sect-mt1 {
  margin-top: 1rem;
}



/*======================================
//--//-->   INTRO
======================================*/
.intro {
  position: relative;
}

.intro .intro-title {
  font-size: 1.8rem;
  font-weight: 300;
  color: #505050;
}

.overlay-intro {
 min-height: 30px;
}

.intro-mf {
  height: 90px;
}










/*======================================
//--//-->   CURRENT
======================================*/

.z-index-5 {
  z-index: 5;
}

.z-index-1 {
  z-index: 1;
}

.current-box {
  border-bottom: 2px solid;
  background-color: rgba(65, 105, 225, 0);
}

.current-list-group-item {
  background-color: rgba(65, 105, 225, 0);
}

.current-card-header {
    padding: 0.5rem 1.25rem;
}






/*======================================
//--//-->   FORECAST
======================================*/

.box-shadow-full {
  padding: 1.25rem 0.5rem;
  position: relative;
  background-color: rgba(255, 255, 255, 0.4);
  margin-bottom: 1.5rem;
  /*z-index: 2*/
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.06), 0 2px 5px 0 rgba(0, 0, 0, 0.2);
}

.forecast-item-box {
  background-color: rgba(65, 105, 225, 0);
}


@media (min-width: 577px) {
  .forecast-item-box {
    margin: .35rem 0;
  }
}

@media (max-width: 577px) {
  .forecast-item-fsize {
    font-size:0.8rem;
  }
}



input {
  border: 1px solid #999;
  border-radius: .25rem;
  padding: 0.5rem;
  width:300px;
}




/*======================================
//--//-->   SEARCH BUTTON
======================================*/
.search-flex {
  display: -webkit-flex;
  display: flex;
}



/*======================================
//--//-->   SEARCH BUTTON
======================================*/



/*======================================
//--//-->   AUTO FILL COMPLETION
======================================*/
.list-group-item-autofill {
  padding: .3rem 1rem;
  font-size: 0.8rem;
  font-weight: 300;
  line-height: 1.5;
}

.list-group-autofill {
  height: 90px;
}



.no-suggestions {
  color: #999;
  padding: 0.5rem;
  height: 90px;
}

.suggestions {
  border: 1px solid #999;
  border-top-width: 0;
  list-style: none;
  margin-top: 0;
  height: 80px;
  overflow-y: auto;
  padding-left: 0;
}

.suggestions > li {
  padding: 0.2rem;
  font-weight: 300;
  line-height: 1rem;
  font-size: 0.8rem;
}

.suggestion-active,
.suggestions li:hover {
  background-color: #008f68;
  color: #fae042;
  cursor: pointer;
  font-weight: 300;
  line-height: 1rem;
  font-size: 0.8rem;
}

.suggestions li:not(:last-of-type) {
  border-bottom: 1px solid #999;
}


